<template>
  <div>
    <v-card elevation="0">
      <v-card-title>
        <v-row>
          <v-col>
            Product Variation
          </v-col>
          <v-col cols="auto">
            <v-btn color="primary" text @click="addVariation">Add</v-btn>
            <variation-form 
              @success="updated"
              v-model="showForm"
              :edit="edit"
            ></variation-form>
          </v-col>
        </v-row>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="dataset"
      >
        <template v-slot:item.image="{ item }">
          <v-avatar size="40">
            <img :src="item.image" />
          </v-avatar>
        </template>
        <template v-slot:item.actions="{ item }">
          <option-buttons
            :api="api.variation"
            :item="item"
            :deletePermanently="true"
            name="variation"
            :edit="true"
            :modifyOnClickEdit="true"
            @onClickEdit="addValueToEdit"
            @changed="updated"
          ></option-buttons>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
import Api from '../routes/api'
export default {
  props: [ 'value' ],

  components: {
    VariationForm: () => import( './VariationForm' )
  },

  data: () => ({
    api: Api,
    headers: [
      {
        text: 'Image',
        align: 'start',
        value: 'image',
      },
      {
        text: 'Description',
        align: 'start',
        value: 'description',
      },
      {
        text: 'Price',
        align: 'start',
        value:'price'
      },
      {
        text: 'Created',
        align: 'start',
        value: 'createdAt',
      },
      {
        text: 'Actions',
        align: 'end',
        value: 'actions',
      },
    ],
    showForm: false,
    edit: {}
  }),

  computed: {
    dataset: {
      get () { return this.value },
      set ( val ) { this.$emit( 'input', val ) }
    }
  },

  methods: {  
    updated () {
      this.$emit( 'updated' )
    },

    addVariation () {
      this.edit = {
        properties: []
      }
      this.showForm = true
    },

    addValueToEdit ( value ) {
      this.edit = { ...value }
      this.showForm = true
    }
  }
}
</script>